* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

li {
  list-style: none;
  color: #fff;
}

a {
  color: #fff;
  text-decoration: none;
}

p {
  text-align: justify;
}

html {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  scroll-behavior: smooth;
  color: #9ca3af;
}

body {
  background-color: #111827;
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 30px;
  color: #fff;
  font-weight: 500;
}

@media (min-width: 280px) and (max-width: 480px) {
  h1 {
    font-size: 40px;
  }

  h2 {
    font-size: 30px;
  }
}
.grid {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  column-gap: 5px;
}

@media (min-width: 280px) and (max-width: 768px) {
  .grid {
    display: flex;
    flex-direction: column-reverse;
  }
}
.container {
  width: 90%;
  margin: 0 auto;
  padding: 80px 0;
}

/* Types of texts */
.textAnimated {
  transition: top 1s ease;
  position: relative;
  top: 20px;
}
.textAnimated::after {
  position: relative;
  display: block;
  content: "";
  background: #4f46e5;
  margin: 10px 0;
  width: 1px;
  height: 4px;
  transition: width 1.5s ease-out;
  box-shadow: 0px 2px 20px 0px #4f46e5;
}

.textAnimated.animation,
.textAnimatedCenter.animation {
  top: 0;
}

.textAnimated.animation::after {
  width: 70px;
}

.textAnimatedCenter.animation::after {
  width: 100px;
}

.text-bold {
  color: #7a7ad6;
}

.textAnimatedCenter {
  position: relative;
  top: 20px;
  width: 100%;
  margin: 20px auto;
  text-align: center;
  transition: top 1s ease-out;
}
.textAnimatedCenter::after {
  display: block;
  content: "";
  background: #4f46e5;
  width: 1px;
  height: 4px;
  margin: 10px auto;
  transition: width 1.5s ease-out;
  box-shadow: 0px 2px 20px 0px #4f46e5;
}

/* NavBar */
header {
  position: absolute;
  width: 100%;
  z-index: 2;
}

nav {
  margin: 0 auto;
  width: 90%;
  display: flex;
  justify-content: space-between;
  padding: 15px;
  align-items: center;
}

html.isMenuOpen .closeMenu {
  visibility: initial;
}

.isMenuOpen {
  overflow: hidden;
}

.showMenu {
  top: 0 !important;
}

.openMenu,
.closeMenu {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  vertical-align: middle;
  height: 55px;
  width: 55px;
  border-radius: 50%;
  border: 3px solid #7a7ad6;
  color: #fff;
}
.openMenu:hover,
.closeMenu:hover {
  cursor: pointer;
}

.openMenu {
  position: fixed;
  right: 5%;
}

.openMenu i,
.closeMenu i,
.mainMenu li a {
  transition: color 0.3s ease;
}

.openMenu:hover > i,
.closeMenu:hover > i,
.mainMenu li a:hover {
  color: #7a7ad6;
}

.closeMenu {
  position: absolute;
  top: 5%;
  right: 5%;
  visibility: hidden;
}

/* Hero */
main {
  height: 100%;
}

.bg {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
}

.bg-1 {
  right: 0;
  background: url(../img/bg-1.svg) no-repeat left center;
}

.bg-2 {
  left: 0;
  background: url(../img/bg-2.svg) no-repeat right center;
}

.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 100vh;
  color: #fff;
  background-color: #161f2d;
}
.hero .hero__title {
  z-index: 1;
  max-width: 80%;
}
.hero .hero__title h1 {
  font-weight: 800;
}

/* ABOUT */
.about__img {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  text-align: center;
  color: #4f46e5;
  position: relative;
  top: -30px;
}
.about__img span {
  font-size: 400px;
  transition: text-shadow 1s;
}
.about__img span:hover {
  text-shadow: 0px 0px 20px rgba(79, 70, 229, 0.7);
}

.about__info h3 {
  max-width: 350px;
  margin: 30px 0;
  font-weight: initial;
  font-size: 1.5em;
  font-style: italic;
}

@media (min-width: 280px) and (max-width: 768px) {
  .about__img span {
    margin-top: 40px;
    font-size: 10rem;
  }
}
/* Ad */
.ad {
  background: url(../img/formes.svg) no-repeat center center;
  background-color: #161f2d;
  background-attachment: fixed;
}
.ad h2 {
  text-align: center;
}
.ad .container {
  width: 50%;
  text-align: center;
  color: #fff;
}
.ad .container p {
  text-align: center;
  font-size: 30px;
  font-style: italic;
}
.ad .container p span {
  font-size: 35px;
  font-weight: bold;
}

@media (min-width: 280px) and (max-width: 480px) {
  .ad {
    background-size: 255%;
  }
  .ad .container {
    width: 90%;
  }
  .ad .container p {
    font-size: 20px;
  }
  .ad .container p span {
    font-size: 25px;
  }
}
/* Skills */
.skills-info {
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  padding-right: 40px;
  border-right: 1px solid #4d4d4d;
}
.skills-info h3 {
  width: 100%;
  text-align: start;
  color: #fff;
  font-size: 2rem;
  font-weight: bold;
  margin: 15px 0;
  text-align: start;
}

.skills-technologies {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 30px;
  color: #4f46e5;
  width: 100%;
}
.skills-technologies h3 {
  font-size: 1.2rem;
  font-weight: 500;
  color: #7a7ad6;
}

.technologies {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: 50px;
  column-gap: 60px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.technologies-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  width: 80px;
  color: #9ca3af;
}
.technologies-icons p {
  margin-top: 5px;
}

@media (min-width: 280px) and (max-width: 768px) {
  .skills .grid {
    flex-direction: column;
    gap: 30px;
  }

  .technologies {
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
  }

  .skills-info {
    height: auto;
    padding: 0;
    border: none;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .technologies {
    column-gap: 10px;
  }

  .technologies-icons {
    height: 100px;
    width: 100px;
  }

  .skills .grid {
    flex-direction: column;
    gap: 30px;
  }

  .item-info p {
    text-align: center;
  }
}
.proyects {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}

.proyects__items {
  width: 100%;
  height: max-content;
  border-radius: 5px;
  border: 3px solid rgba(31, 41, 55, 0.5);
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  gap: 10px;
  padding: 25px;
}
.proyects__items img {
  padding: 5px;
  margin: 0 auto;
  width: 200px;
  height: 200px;
  border-radius: 15px;
  object-fit: cover;
}

.item .item__title {
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}
.item .item__description {
  margin-bottom: 20px;
}
.item .item__languages {
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
}
.item .item__languages P {
  font-weight: bold;
  color: #fff;
  padding: 10px 15px;
  background-color: #7a7ad6;
  border-radius: 10px;
}
.item .item__src a {
  font-weight: bold;
  color: #fff;
  padding: 10px 15px;
  border-radius: 10px;
  background: linear-gradient(270deg, #10b981, #12eca4);
}
.item .item__src a:hover {
  opacity: 0.8;
}

@media (min-width: 280px) and (max-width: 1024px) {
  .proyects {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .proyects__items {
    flex-direction: column;
    height: max-content;
  }
  .proyects__items img {
    width: 90%;
    height: 90%;
  }

  .item__title {
    text-align: center;
  }

  .item__src,
.item__languages {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
  }
}
#contact {
  background-color: #161f2d;
  height: 100vh;
  margin: 0 auto;
}
#contact .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 100%;
}
#contact .contact-principal {
  width: 100%;
}

.contact-form {
  margin: 0 auto;
  width: 50%;
}

.form-name,
.form-email,
.form-message {
  display: flex;
  flex-direction: column;
}
.form-name input,
.form-name textarea,
.form-email input,
.form-email textarea,
.form-message input,
.form-message textarea {
  border: none;
  background: #1f2937;
  border: 2px solid #374151;
  height: 40px;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 5px;
  color: #9ca3af;
}
.form-name input:focus,
.form-name textarea:focus,
.form-email input:focus,
.form-email textarea:focus,
.form-message input:focus,
.form-message textarea:focus {
  outline: none;
  border-color: #4f46e5;
  box-shadow: 0px 0px 5px 0px #4f46e5;
}

.form-ready {
  border: 2px solid #4f46e5 !important;
  box-shadow: 0px 0px 5px 0px #4f46e5 !important;
}

.message {
  resize: none;
  height: initial !important;
}

.form-submit {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.form-submit button {
  color: #fff;
  border: none;
  padding: 15px;
  background-color: #4f46e5;
  width: 100%;
  text-align: center;
  border-radius: 6px;
  transition: opacity 0.5s ease-out;
}
.form-submit button:hover {
  cursor: pointer;
  opacity: 0.8;
}

.contact-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.contact-icon i {
  font-size: 200px;
  color: #4f46e5;
  transition: text-shadow 1s;
}
.contact-icon i:hover {
  text-shadow: 0px 0px 20px rgba(79, 70, 229, 0.7);
}

.msg-succes,
.msg-error {
  margin: 20px auto;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 15px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  position: relative;
  top: 70px;
  opacity: 0;
}

.msg-animated {
  animation: getIn 2s forwards;
}

.msg-succes {
  background-color: #4fc08d;
}

.msg-error {
  background-color: #cb3837;
}

@keyframes getIn {
  from {
    top: 70px;
    opacity: 0;
  }
  to {
    top: 0px;
    opacity: 1;
  }
}
@keyframes getOut {
  from {
    top: 0px;
    opacity: 1;
  }
  to {
    top: 70px;
    opacity: 0;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .contact {
    height: 100%;
  }

  .contact-form {
    width: 90%;
  }

  .msg-succes,
.msg-error {
    width: 90%;
  }
}
@media (min-width: 280px) and (max-width: 767px) {
  .contact-form {
    width: 90%;
  }

  .form-submit button {
    width: 100%;
  }

  .contact-icon {
    display: none;
  }

  .msg-succes,
.msg-error {
    width: 90%;
  }
}
footer {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.footer-info {
  margin: 0 auto;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  color: #9ca3af;
}
.footer-info p {
  font-size: 1.3rem;
  font-weight: bold;
}
.footer-info i {
  font-size: 2em;
}
.footer-info .social-net {
  gap: 5px;
}
.footer-info .social-net a {
  color: #9ca3af;
}

@media (min-width: 280px) and (max-width: 768px) {
  .footer-info {
    flex-direction: column;
    gap: 20px;
  }
  .footer-info p {
    padding-bottom: 10px;
    border-bottom: 1px solid #fff;
  }
  .footer-info i {
    font-size: 1.5em;
  }
}
.btn {
  display: inline-block;
  padding: 1em 3em;
  text-transform: uppercase;
  color: #4f46e5;
  font-weight: bold;
  text-decoration: none;
  border: 2px solid #4f46e5;
  box-shadow: 0px 2px 20px 0px rgba(190, 24, 93, 0.5);
  border-radius: 87px;
  letter-spacing: 1px;
  transition: box-shadow ease-in 0.2s;
}

.btn-green {
  margin-top: 30px;
  color: #fff;
  background: #4f46e5;
  border: 1px solid #4f46e5;
  box-shadow: 0px 2px 20px 0px rgba(79, 70, 229, 0.5);
}

.btn-green:hover {
  cursor: pointer;
  box-shadow: 0px 2px 20px 0px #4f46e5;
}

/* Menu hamburger */
.mainMenu {
  height: 100vh;
  position: fixed;
  top: -110vh;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 40px;
  transition: top 0.8s ease;
  background-color: #161f2d;
}
.mainMenu li a {
  font-size: 2.5rem;
  color: #9ca3af;
}

/* Iconos */
.social-net {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.social-net a i {
  margin-right: 5px;
  transition: color 0.5s ease-out;
}
.social-net a:hover i {
  color: #7a7ad6;
}